.item-catalog-teaser2 {
  display: flex;
  flex-flow: row wrap;
  overflow: clip;
  outline: 1px solid #cdcdcd;
  border: 0px solid transparent;
  align-items: flex-start;
  align-content: start;
}

.item-catalog-teaser2:hover:before {
  background: #BB3939;
}

.item-catalog-teaser2:hover {
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  z-index: 2;
}

.item-catalog-teaser2:hover .item__title h3 {
  color: #AE2121;
}

.item-catalog-teaser2:hover .item__link img {
  transform: scale(1.25);
  transition: ease-in-out 0.3s;
}

.item-catalog-teaser2:before {
  content: '';
  width: 100%;
  height: 145px;
  position: absolute;
  background: #F8F8F8;
  top: 0;
}

.item-catalog-teaser2:after {
  content: '';
  width: 100%;
  height: calc(100% - 145px);
  position: absolute;
  background: #fff;
  bottom: 0;
}

.item-catalog-teaser2 .item__link {
  padding: 23px 0 0;
  flex-flow: row wrap;
  z-index: 1;
  display: flex;
  border: 0;
  flex: 1;
  margin-bottom: 9px;
}

.item-catalog-teaser2 .item__link .item__title {
  flex: 1 100%;
}

.item-catalog-teaser2 .item__link .item__title h3 {
  font-size: 15px;
  margin: 34px 18px 0;
  text-align: left;
  line-height: 28px;
  font-weight: 700;
}

.item-catalog-teaser2 .item__image {
  height: 192px;
  position: relative;
  display: flex;
  margin-top: 16px;
  flex: 1 100%;
}

.item-catalog-teaser2 .item__image img {
  width: 100%;
  object-fit: contain;
  /*! margin: 0 auto; */
}

.item-catalog-teaser2 .item__desc {
  z-index: 1;
  padding: 0 18px;
  flex: 1 100%;
}

.item-catalog-teaser2 .item__desc > *:first-child {
  margin-top: 0;
}

.item-catalog-teaser2 .item__desc ul,
.item-catalog-teaser2 .item__desc p {
  margin: 10px 0;
}

.item-catalog-teaser2 .item__desc > *:last-child {
  margin-bottom: 38px;
}

@media only screen and (min-width: 768px) {
  .item-catalog-teaser2:before {
    height: 210px;
  }

  .item-catalog-teaser2:after {
    height: calc(100% - 210px);
  }

  .item-catalog-teaser2 .item__link {
    padding: 40px 0 0;
  }
}

@media only screen and (min-width: 1024px) {
  .item-catalog-teaser2 .item__link {
    padding: 45px 0 0;
  }

  .item-catalog-teaser2 .item__link .item__title h3 {
    margin: 54px 25px 0;
    font-size: 20px;
    line-height: 28px;
  }

  .item-catalog-teaser2 .item__desc {
    padding: 0 25px;
  }
}

@media only screen and (min-width: 1280px) {
  .item-catalog-teaser2 .item__link .item__title h3 {
    margin: 54px 39px 0;
  }

  .item-catalog-teaser2 .item__desc {
    padding: 0 39px;
  }
}